.recipe{
    border: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    margin: 10px;
    box-shadow: 0px 5px 20px rgb(71,71,71) ;
    align-items: center;
    min-width: 40%;
    padding: 10px;
}

.image{
    border-radius: 50%;
    width: 150px;
    height: 150px;
}

@media (max-width:1025px) {
    .recipe {
        width: 90%;
        margin: 20px auto
    }
}